.pdf-view-box{
  text-align: center;
  background-color: #f4f4f4;
  padding: 10px 10px 25px 10px;
  margin-top: 10px;
}

.pdf-container {
  display: flex;
  justify-content: center;
  user-select: none;
}

.controls {
  margin: 20px;
  color: #505050;
  font-weight:500;
}

.controls button{
  color: #fff;
  background-color: #3f89ea;
  border-radius: 10px;
  padding: 0 10px;
  height: 35px;
  width: 40px;
  font-weight: bold;
  border: 0;
  outline: none;
}

.react-pdf__Page {
  border: 1px solid #ebebeb;
}

@media print {
  .pdf-view-box {
    display: none;
  }
}
