.user-row { display: flex;   justify-content: center;}
.user-box {
    text-align: center;
    width: 25%;
    border: 1px solid #cccccc24;
    border-radius: 10px;
    padding: 20px;
    margin: 0 15px;
    background: #ededed2b;
}

header.App-header nav {
    background:#25aae1; 
    background-image: linear-gradient(to right, #25aae1, #4481eb, #04befe, #3f86ed);
}

.user-box img {
    height: 100px;
    margin-bottom: 0;
    width: 100px;
    padding: 0;
    background: #f7f7f7;
    border-radius: 100%;
    object-fit: contain;
}

.home-heading h2 {
    text-align: center;
    margin: 20px 0 40px;
    font-weight: 700;
}



.btn {
    min-width: 200px;
    max-width:100%;
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    margin: 20px 0 0;
    height: 55px;
    text-align:center;
    border: none;
    background-size: 300% 100%;

    border-radius: 50px;
    moz-transition: all .4s ease-in-out;
    -o-transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
    background-image: linear-gradient(to right, #25aae1, #4481eb, #04befe, #3f86ed);
    box-shadow: 0 4px 15px 0 rgba(65, 132, 234, 0.75);
}

.btn:hover {
    background-position: 100% 0;
    moz-transition: all .4s ease-in-out;
    -o-transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}


