.toggle{display: none;}
input#side { display: none;}

.homepage-pageData { padding: 0; margin: 15px 0 0;}
.logout-btn { margin: 0;  min-width: 40px;  width: 40px;  background: #ffffff; height: 40px; border-radius: 100%;color: #4183ec; cursor: pointer;}
