button.navbar-toggler {border: 1px solid #fff !important; outline: none;}
    

@media screen and (max-width: 1024px) {   
.toggle{display: block;}

.page-wrap { overflow-x: hidden; line-height: 26px; }  
  .page-content {  position: relative;  z-index: 0;  padding: 20px 10px 20px 10px; -webkit-transition: 0.6s;  -moz-transition: 0.6s;  transition: 0.6s;  }
    .toggle { -webkit-transition: 0.5s; -moz-transition: 0.5s; transition: 0.5s; text-decoration: none;  font-size: 35px; color: black; z-index: 1;  }
  .toggle:hover { color: RGB(179, 2, 35);  }  
  .sidebar {  position: absolute;  top: 0px;  right: 0; bottom: 0px;  left: -160px;  -webkit-transition: 0.6s; -moz-transition: 0.6s; transition: 0.6s; z-index: 0;  }
  .sidebar li { -webkit-font-smoothing: antialiased; -webkit-transition: 1s; -moz-transition: 1s; transition: 1s; list-style: none; }
   #side { display: none;  }
  #side:checked + .page-wrap .sidebar { transform: translateX(160px); }
  /* #side:checked + .page-wrap .sidebar li {  font-size: 16px;  } */
  #side:checked + .page-wrap .page-content { transform: translateX(160px);  }
  label.toggle {
    /* border: 1px solid; */
    width: 40px;
    padding: 4px;
    font-size: 27px;
    height: 36px;
    text-align: center;
    line-height: 26px;
    border-radius: 5px;
    background: #4283ea;
    color: #fff;
}

} 




@media screen and (max-width:991px) 
{  
.add-table {width: 100%; overflow-x: auto; }
