.side-bar-menu ul li button { padding: 10px 8px;  line-height: 1;}
.menu-user .user-img {width: 60px; height: 60px;}

}    

@media screen and (max-width: 767px) 
{  

.dashboard-content {width: 100%; }
.dashboard-summary-details .btn { margin-bottom: 20px;}
#side:checked + .page-wrap .sidebar { transform: translateX(130px);}
#side:checked + .page-wrap .page-content {transform: translateX(130px);}
.sidebar {left: -130px;}
.side-bar-menu {width: 125px}
.side-bar-menu ul li button i {font-size: 20px; margin-bottom: 4px;}
.side-bar-menu ul li button { padding: 10px 8px;  line-height: 1;}
